export default {
  data: () => ({
    form: {},
    error: null,
  }),
  computed: {
    transactions() {
      return this.$store.state.transaction.transactions
    },
  },
  async created() {
    await this.$store.dispatch('transaction/fetchNotReturnedTransactions')
  },

  methods: {
    async click() {
      await this.$store
        .dispatch('transaction/return', this.form.id)
        .then(() => {
          return this.$router.push('/history-transaction')
        })
        .catch((e) => {
          this.error = e
        })
    },
  },
}
